* {
    font-family: "Saira Semi Condensed", sans-serif;
    transition: all 0.3s ease;
}

form {
    width: 400px;
    display: flex;
    flex-flow: column wrap;
    align-items: center;
    justify-content: center;
}

form div,
form label,
form input {
    width: 100%;
}

label,
input {
    padding: 8px;
    margin-bottom: 10px;
}

label {
    color: #555;
    margin: 0 10px 0 0;
}

input {
    background: rgba(255, 255, 255, 0.5);
    border: none;
    border-radius: 4px;
    box-shadow: 0 8px 6px -6px #555;
}

input:focus {
    background: white;
    box-shadow: none;
}

